<template>
  <div class="account-table__wrapper">
    <div class="account-table__filter">
      <el-form
        :model="form"
        :rules="rules"
        ref="filterForm"
        label-width="100px"
        class="form"
        label-position="right"
        :inline="true"
      >
        <el-form-item label="" prop="consumeType">
          <el-select v-model="form.consumeType" clearable placeholder="">
            <el-option
              v-for="(label, value) in $DIC.consumeTypes"
              :key="value"
              :label="label"
              :value="String(value)"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="consumeTime">
          <el-date-picker
            type="daterange"
            placeholder=""
            v-model="form.consumeTime"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="consumer">
          <el-select v-model="form.consumer" clearable placeholder="">
            <el-option
              v-for="(label, value) in $DIC.personNames"
              :key="value"
              :label="label"
              :value="value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleQuery"></el-button>
          <el-button @click="handleReset"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="account-table__operate">
      <div class="btns">
        <el-button type="success" @click="handleExport"></el-button>
        <el-button type="primary" @click="handleAdd"></el-button>
        <el-button type="danger" @click="handleMultiDelete"></el-button>
      </div>
    </div>
    <div class="account-table__container">
      <el-table
        :data="accountData"
        border
        height="100%"
        max-height="100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection"></el-table-column>
        <el-table-column type="index" label="" width="80"></el-table-column>
        <el-table-column
          v-for="item in tableColumns"
          :key="item.value"
          :label="item.label"
          :prop="item.value"
          v-bind="item"
        >
          <template slot-scope="{ row }">
            {{ row[item.value] | columnFilter(item.value, vm) | deVal }}
          </template>
        </el-table-column>
        <el-table-column label="" fixed="right" width="100">
          <template slot-scope="{ row }">
            <el-link type="primary" @click="handleEdit(row)"></el-link
            >&nbsp;&nbsp;
            <el-link type="danger" @click="handleDelete(row.id)"></el-link>
          </template>
        </el-table-column>
      </el-table>
      <div class="account-table__footer">
        <el-pagination
          @size-change="pageSizeChange"
          @current-change="pageNoChange"
          :current-page="pageNum"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
        />
      </div>
    </div>

    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      width="30%"
      top="5vh"
      class="dialog"
    >
      <el-form
        :model="addForm"
        :rules="addRules"
        ref="dialogForm"
        label-width="120px"
        class="form"
        label-position="left"
      >
        <el-form-item label="" prop="consumeType">
          <el-select v-model="addForm.consumeType" placeholder="">
            <el-option
              v-for="(label, value) in $DIC.consumeTypes"
              :key="value"
              :label="label"
              :value="value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="consumeName">
          <el-input v-model="addForm.consumeName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="consumeSum">
          <el-input v-model="addForm.consumeSum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="consumeTime">
          <el-date-picker
            type="date"
            placeholder=""
            v-model="addForm.consumeTime"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="consumer">
           <el-select v-model="addForm.consumer" clearable placeholder="">
            <el-option
              v-for="(label, value) in $DIC.personNames"
              :key="value"
              :label="label"
              :value="value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isSpecial">
          <el-radio v-model="addForm.isSpecial" :label="true"></el-radio>
          <el-radio v-model="addForm.isSpecial" :label="false"></el-radio>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input
            type="textarea"
            :rows="2"
            placeholder=""
            v-model="addForm.remark"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleSave"> </el-button>
        <el-button @click="handleCancel"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  queryOriginalData,
  saveOriginalData,
  deleteOriginalData,
  editOriginalData,
} from "@/api/account-table.js";
import moment from "moment";
const _ = require("lodash");

const tableColumns = [
  { label: "", value: "consumeType", width: "120" },
  { label: "", value: "consumeName", width: "200" },
  { label: "", value: "consumeSum", width: "120" },
  { label: "", value: "consumeTime", width: "180" },
  { label: "", value: "consumer", width: "120" },
  { label: "", value: "isSpecial", width: "120" },
  { label: "", value: "remark" },
];

export default {
  name: "AccountTable",
  inject: ["$DIC"],
  filters: {
    columnFilter: (value, prop, vm) => {
      if (prop === "isSpecial" && _.isBoolean(value)) {
        return value ? "" : "";
      } else if (prop === "consumeType" && value) {
        return vm.$DIC["consumeTypes"][value];
      } else if (prop === "consumer" && value) {
        return vm.$DIC["personNames"][value];
      }
      return value;
    },
  },
  data() {
    return {
      vm: this,
      tableColumns: Object.freeze(tableColumns),
      accountData: [],
      pageNum: 1,
      pageSize: 20,
      total: 0,
      dialogVisible: false,
      form: {
        consumeType: "",
        consumeTime: [
          moment(`${moment().format("YYYY-MM")}-10`)
            .subtract(1, "months")
            .format("YYYY-MM-DD"),
          moment(`${moment().format("YYYY-MM")}-10`).format("YYYY-MM-DD"),
        ],
        consumer: "",
      },
      addForm: {
        consumeType: "",
        consumeName: "",
        consumeSum: "",
        consumeTime: "",
        consumer: "",
        isSpecial: false,
        remark: "",
      },
      rules: {},
      addRules: {},
      dialogTitle: "",
      selections: [],
    };
  },
  mounted() {
    this.getAccountData();
  },
  methods: {
    handleQuery() {
      this.pageSize = 20;
      this.pageNum = 1;
      this.getAccountData();
    },
    handleReset() {
      this.form = {
        consumeType: "",
        consumeTime: [
          moment(`${moment().format("YYYY-MM")}-10`)
            .subtract(1, "months")
            .format("YYYY-MM-DD"),
          moment(`${moment().format("YYYY-MM")}-10`).format("YYYY-MM-DD"),
        ],
        consumer: "",
      };
      this.pageNum = 1;
      this.pageSize = 20;
      this.getAccountData();
    },
    handleSelectionChange(selection) {
      this.selections = selection;
    },
    handleMultiDelete() {
      if (!this.selections.length) {
        this.$message({
          type: "warning",
          message: "!",
        });
        return;
      }
      const ids = this.selections.map((item) => item.id).join(",");
      this.handleDelete(ids);
    },
    handleDelete(ids) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          deleteOriginalData({ ids })
            .then(() => {
              this.$message({
                type: "success",
                message: "!",
              });
              this.getAccountData();
            })
            .catch((err) => {
              console.log(err);
            });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    handleEdit(data) {
      this.addForm = { ...data };
      this.dialogVisible = true;
      this.dialogTitle = "";
    },
    handleSave() {
      const data = {
        ...this.addForm,
      };
      const func = this.addForm.id ? editOriginalData : saveOriginalData;
      func(data).then(() => {
        this.getAccountData();
        this.$message({
          message: "",
          type: "success",
        });
        this.dialogVisible = false;
        this.addForm = {};
      });
    },
    handleCancel() {
      this.resetForm();
      this.dialogVisible = false;
    },
    resetForm() {
      this.addForm = {
        consumeType: "",
        consumeName: "",
        consumeSum: "",
        consumeTime: "",
        consumer: "",
        isSpecial: false,
        remark: "",
      };
    },
    handleAdd() {
      this.resetForm();
      this.dialogVisible = true;
      this.dialogTitle = "";
    },
    pageSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.getAccountData();
    },
    pageNoChange(pageNum) {
      this.pageNum = pageNum;
      this.getAccountData();
    },
    getAccountData() {
      const params = {
        pageNum: this.pageNum,
        pageSize: this.pageSize,
        ...this.form,
      };
      queryOriginalData(params)
        .then((res) => {
          this.accountData = res.data;
          this.total = res.total;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    handleExport() {
      this.$router.push({path: "/upload"});
    },
  },
};
</script>

<style lang="scss" scoped>
.account-table__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 16px 32px;
  .account-table__operate {
    width: 100%;
    height: 64px;
    line-height: 64px;
    display: inline-flex;
    justify-content: space-between;
    .btns {
      .el-button {
        width: 88px;
        min-width: 88px;
        height: 32px;
        line-height: 32px;
        padding: 0;
      }
    }
  }
  .account-table__filter {
    .form {
      .el-form-item {
        &:last-child {
          margin-left: 32px;
        }
      }
    }
  }
  .account-table__container {
    height: calc(100% - 168px);
    .el-table {
      width: 100%;
    }
  }
  .account-table__footer {
    position: absolute;
    height: 56px;
    line-height: 56px;
    bottom: 0;
    left: 32px;
    right: 32px;
    .el-pagination {
      display: flex;
      align-items: center;
      ::v-deep .el-pager li {
        vertical-align: middle;
      }
    }
  }
  .el-dialog {
    .form {
      .el-select,
      .el-date-editor {
        width: 100%;
      }
    }
  }
}
</style>